import { Box } from "components/Box/Box"
import { MyChart } from "components/Chart/Chart"
import { RangeStorage } from "components/Range/RangeStorage"
import { RangeTransfer } from "components/Range/RangeTransfer"
import { RangeHddSdd } from "components/Range/RangeHddSdd"
import { RangeMultiSingle } from "components/Range/RangeMultiSingle"
import { useMemo, useState } from "react"
import { Med } from "./App.styled"

const backblazePrice = {
  minPrice: 7,
  storagePrice: 0.005,
  transferPrice: 0.01,
};

const bunnyPrice = {
  maxPrice: 10,
  storagePriceHdd: 0.01,
  storagePriceSdd: 0.02,
  transferPrice: 0.01,
};

const scalewayPrice = {
  storagePriceMulti: 0.06,
  storagePriceSingle: 0.03,
  transferPrice: 0.02,
  firstFreeGb: 75,
};

const vultrPrice = {
  minPrice: 5,
  storagePrice: 0.01,
  transferPrice: 0.01,
};

export const App = () => {
  const [valueRangeStorage, setValueRangeStorage] = useState(0);
  const [valueRangeTransfer, setValueRangeTransfer] = useState(0);
  const [hddSdd, sethddSdd] = useState(0);
  const [multiSingle, setMultiSingle] = useState(0);


  const rangeStorageValue = (value) => {
    setValueRangeStorage(value[0]);
  };

  const rangeTransferValue = (value) => {
    setValueRangeTransfer(value[0]);
  };

  const rangeHddSddValue = (value) => {
    sethddSdd(value[0]);
  };

  const rangeMultiSingleValue = (value) => {
    setMultiSingle(value[0]);
  };

  const calculator = (price, range) => {
    return price * range
  };

  const fullBackblazePrice = useMemo(() => {
    return (calculator(backblazePrice.storagePrice, valueRangeStorage) + calculator(backblazePrice.transferPrice, valueRangeTransfer)) < backblazePrice.minPrice
      ? backblazePrice.minPrice : calculator(backblazePrice.storagePrice, valueRangeStorage) + calculator(backblazePrice.transferPrice, valueRangeTransfer);
  }, [valueRangeStorage, valueRangeTransfer])

  const fullBunnyPrice = useMemo(() => {
    return ((hddSdd ? calculator(bunnyPrice.storagePriceSdd, valueRangeStorage) : calculator(bunnyPrice.storagePriceHdd, valueRangeStorage))
      + calculator(bunnyPrice.transferPrice, valueRangeTransfer)) > 10
      ? 10 : ((!hddSdd ? calculator(bunnyPrice.storagePriceHdd, valueRangeStorage)
        : calculator(bunnyPrice.storagePriceSdd, valueRangeStorage)) + calculator(bunnyPrice.transferPrice, valueRangeTransfer));
  }, [hddSdd, valueRangeStorage, valueRangeTransfer])

  const fullScaleway = useMemo(() => {
    return (valueRangeStorage <= scalewayPrice.firstFreeGb ? 0 : (multiSingle ? calculator(scalewayPrice.storagePriceMulti, (valueRangeStorage - scalewayPrice.firstFreeGb))
      : calculator(scalewayPrice.storagePriceSingle, (valueRangeStorage - scalewayPrice.firstFreeGb))))
      + (valueRangeTransfer <= scalewayPrice.firstFreeGb ? 0 : calculator(scalewayPrice.transferPrice, (valueRangeTransfer - scalewayPrice.firstFreeGb)));
  }, [multiSingle, valueRangeStorage, valueRangeTransfer])

  const fullVultrPrice = useMemo(() => {
    return (calculator(vultrPrice.storagePrice, valueRangeStorage) + calculator(vultrPrice.transferPrice, valueRangeTransfer)) < vultrPrice.minPrice
      ? vultrPrice.minPrice : calculator(vultrPrice.storagePrice, valueRangeStorage) + calculator(vultrPrice.transferPrice, valueRangeTransfer);
  }, [valueRangeStorage, valueRangeTransfer])

  return (
    <Box display='flex' justifyContent='center' px='7' py='6'>
      <Box flex='0 1 800px'>
        <div>
          <MyChart backblazeCalc={fullBackblazePrice} bunnyCalc={fullBunnyPrice} scalewayCalc={fullScaleway} vultrCalc={fullVultrPrice} />
          <Box display='flex' justifyContent='center' flexWrap='wrap' gridGap='30px' mt='5' mb='7'>
            <RangeHddSdd valueHddSdd={rangeHddSddValue} />
            <RangeMultiSingle valueMultiSingle={rangeMultiSingleValue} />
          </Box>
        </div>
        <Med>
          <RangeStorage valueStorage={rangeStorageValue} />
          <RangeTransfer valueTransfer={rangeTransferValue} />
        </Med>
      </Box>
    </Box>
  );
};
