import { Box } from 'components/Box/Box';
import * as React from 'react';
import { Range, getTrackBackground } from 'react-range';

const STEP = 1;
const MIN = 0;
const MAX = 1;

export class RangeHddSdd extends React.Component {
   state = { values: [0] };
   render() {
      return (
         <Box display='flex' flexDirection='column' alignItems='center' gridGap='4'>
            <span>Bunny</span>
            <Box display='flex' alignItems='center' gridGap='25px'>
               <span>HDD</span>
               <Range
                  step={STEP}
                  min={MIN}
                  max={MAX}
                  values={this.state.values}
                  onChange={(values) => [this.setState({ values }), this.props.valueHddSdd({ ...values })]}
                  renderTrack={({ props, children }) => (
                     <div
                        {...props}
                        style={{
                           ...props.style,
                           height: '25px',
                           width: '45px',
                           background: getTrackBackground({
                              values: this.state.values,
                              colors: ["#548BF4", "#ccc"],
                              min: MIN,
                              max: MAX
                           }),
                        }}>
                        {children}
                     </div>
                  )}
                  renderThumb={({ props }) => (
                     <div
                        {...props}
                        style={{
                           ...props.style,
                           height: '30px',
                           width: '30px',
                           backgroundColor: '#999'
                        }} />
                  )} />
               <span>SDD</span>
            </Box>
         </Box>
      );
   };
};