import React, { useMemo } from 'react';
import {
   Chart as ChartJS,
   CategoryScale,
   LinearScale,
   BarElement,
   Title,
   Tooltip,
   Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';


ChartJS.register(
   CategoryScale,
   LinearScale,
   BarElement,
   Title,
   Tooltip,
   Legend
);

export const MyChart = ({ backblazeCalc, bunnyCalc, scalewayCalc, vultrCalc }) => {

   const datasets = useMemo(() => {

      const dataSet = [
         {
            label: 'backblaze.com, $',
            data: [backblazeCalc],
            backgroundColor: ['red'],
            maintainAspectRatio: false,
         },
         {
            label: 'bunny.net, $',
            data: [bunnyCalc],
            backgroundColor: ['orange'],
            maintainAspectRatio: false,
         },
         {
            label: 'scaleway.com, $',
            data: [scalewayCalc],
            backgroundColor: ['violet'],
            maintainAspectRatio: false,
         },
         {
            label: 'vultr.com, $',
            data: [vultrCalc],
            backgroundColor: ['lightblue'],
            maintainAspectRatio: false,
         },
      ]

      return dataSet.sort((a, b) => a.data - b.data).map(item => {
         if (dataSet.indexOf(item) !== 0) {
            return { ...item, backgroundColor: ['#ccc'] };
         };
         return item
      });
   }, [backblazeCalc, bunnyCalc, scalewayCalc, vultrCalc])


   const data = {
      labels: [''],
      datasets,
   };

   const options = {
      responsive: true,
      plugins: {
         legend: {
            position: 'bottom',
         },
      },
   };

   return <Bar height={200} options={options} data={data} />;
};