import * as React from 'react';
import { Range, getTrackBackground } from 'react-range';

const STEP = 1;
const MIN = 0;
const MAX = 1000;

export class RangeStorage extends React.Component {
   state = { values: [0] };

   render() {
      return (
         <div style={{
            display: "flex",
            justifyContent: "center",
            flexWrap: "wrap",
            width: '100%'
         }}>
            <Range
               step={STEP}
               min={MIN}
               max={MAX}
               values={this.state.values}
               onChange={(values) => [this.setState({ values }), this.props.valueStorage({ ...values })]}
               renderTrack={({ props, children }) => (
                  <div
                     {...props}
                     style={{
                        ...props.style,
                        height: '18px',
                        width: '90%',
                        background: getTrackBackground({
                           values: this.state.values,
                           colors: ["#151d2b", "#ccc"],
                           min: MIN,
                           max: MAX
                        }),
                     }}>
                     {children}
                  </div>
               )}
               renderThumb={({ props }) => (
                  <div
                     {...props}
                     style={{
                        ...props.style,
                        height: '30px',
                        width: '30px',
                        borderRadius: '100%',
                        backgroundColor: '#999',
                        boxShadow: '0px 0px 10px 5px rgba(0,0,0,0.75)',
                     }} />
               )} />
            <output style={{ marginTop: "15px" }} id="outputV1">
               <span>Storage: {this.state.values[0].toFixed()} Gb</span>
            </output>
         </div>
      );
   };
};